	import uCharts from '@/components/u-charts/u-charts.js';
	var _self;
	var canvaLineA=null;
  export default {
    data () {
      return {
		cWidth:'',
		cHeight:'',
		pixelRatio:1,
		url:"",
		monthData:"",
      }
    },
    onLoad: function(options) {
      this.url=this.$consturl;
      _self = this;
      this.cWidth=uni.upx2px(750);
      this.cHeight=uni.upx2px(500);
      this.getServerData(options.deviceid);
    },
	methods:{
		getServerData(deviceid){
			let that=this,lineData;
			  uni.request({
			  url: that.url+'web/api/findHistoryReportMonth',
			  header: {
			    'content-type': 'application/json'
			  },
			  data: {deviceid:deviceid},
			  method: 'get', // OPTIONS, GET, HEAD, POST, PUT, DELETE, TRACE, CONNECT
			  // header: {}, // 设置请求的 header
			  success: function(res){
			    that.monthData=res.data;
			    let LineA={categories:[],series:[]};
			    let asdasda={"name": "","data":""};
			    let lineD=res.data.zheXian;
			    let o=[];
			    o[0]=lineD.findTime01_10;o[1]=lineD.findTime11_20;o[2]=lineD.findTime21_30;
			  lineData=o;
			  asdasda.data=lineData;
			  asdasda.name="每天的上网时长";
			  LineA.categories=['上旬', '中旬', '下旬']
			  LineA.series.push(asdasda);
			  _self.showLineA("canvasLineA",LineA);
			  },
			  fail: function() {
			    // fail
			    console.log('history请求资源......fail');
			  }
			  
			});
		},
		showLineA(canvasId,chartData){
			canvaLineA=new uCharts({
				$this:_self,
				canvasId: canvasId,
				type: 'line',
				fontSize:11,
				legend:{show:true},
				dataLabel:false,
				dataPointShape:true,
				background:'#FFFFFF',
				pixelRatio:_self.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				xAxis: {
					type:'grid',
					gridColor:'#CCCCCC',
					gridType:'dash',
					dashLength:8
				},
				yAxis: {
					gridType:'dash',
					gridColor:'#CCCCCC',
					dashLength:8,
					splitNumber:5,
					min:10,
					max:180,
					format:(val)=>{return val.toFixed(0)+'秒'}
				},
				width: _self.cWidth*_self.pixelRatio,
				height: _self.cHeight*_self.pixelRatio,
				extra: {
					line:{
						type: 'straight'
					}
				}
			});
			
		},
		touchLineA(e) {
			canvaLineA.showToolTip(e, {
				format: function (item, category) {
					return category + ' ' + item.name + ':' + item.data 
				}
			});
		},
	},
    onShareAppMessage () {}
  }
